import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ModalController } from '@ionic/angular';
import { format, parseISO } from 'date-fns';
import * as moment from 'moment';

@Component({
  selector: 'app-daterange',
  templateUrl: './daterange.page.html',
  styleUrls: ['./daterange.page.scss'],
})
export class DaterangePage implements OnInit {
  appointmentForm: FormGroup;
  fromDate: any;
  toDate: any;
  maxDate: String = new Date().toISOString();
  constructor(private modalCtrl: ModalController, private fb: FormBuilder,) { }

  ngOnInit() {
    this.appointmentForm = this.fb.group({
      fromDate: [null, [Validators.required]],
      toDate: [null, [Validators.required]]
    });
  }

  
  async next() {
    await this.modalCtrl.dismiss();
  }
  getfromDate(event: any) {
    console.log("event", event)
    let fromDate = format(parseISO(event), 'yyyy-MM-dd');
    this.appointmentForm.get('fromDate').patchValue(fromDate);
  }
  getToDate(event: any) {
    console.log("to event", event)
    let toDate = format(parseISO(event), 'yyyy-MM-dd');
    this.appointmentForm.get('toDate').patchValue(toDate);
  }

  onClickConfirm() {
    let data = {
      fromDate:  this.appointmentForm.get('fromDate').value ? this.appointmentForm.get('fromDate').value : moment(new Date()).format("YYYY-MM-DD"),
      toDate: this.appointmentForm.get('toDate').value ? this.appointmentForm.get('toDate').value : moment(new Date()).format("YYYY-MM-DD")
    };
    this.modalCtrl.dismiss(data);
  }


}
